<template>
	<view>
		<cu-custom bgColor="bg-gradual-green" :isBack="true">
			<view slot="backText">返回</view>
			<view slot="content">个人主页</view>
		</cu-custom>
		<!-- 页面滚动 -->
		<scroll-view scroll-y @scrolltolower="onRefresh" @scroll="scroll" style="height: 100vh">
			<!-- 头部 -->
			<view class="my-header">
				<view class="my-header_container flex flex-space-between">
					<image src="/static/logo.png" class="user-photo" mode="aspectFit"></image>
					<view class="my-header-right padding-lr flex-1">
						<view class="nickname text-black text-bold">我是昵称</view>
						<view>我就是一个签名</view>
					</view>
					<view class="flex flex-align-center">
						<view class="cu-tag line-green">+ 关注</view>
					</view>
				</view>
				<view class="cu-list grid col-2 no-border">
					<view class="cu-item" @click="onGoPage({
						link: '/pages/my/focus'
					})">
						<text>关注</text>
						<text>{{ focus.focusNum }}</text>
					</view>
					<view class="cu-item"  @click="onGoPage({
						link: '/pages/my/follow'
					})">
						<text>粉丝</text>
						<text>{{ focus.followNum }}</text>
					</view>
				</view>
			</view>
			<!-- 我的发布 -->
			<view :class="['margin-top-sm', {'fixed': fixed}]" :style="{top: customBar + 'px'}">
				<view class="cu-bar bg-white">
					<view class="action text-black text-bold">
						我的发布
					</view>
				</view>
				<scroll-view scroll-x class="bg-white nav text-center">
					<view :class="['cu-item', {'text-green cur': index == curTab}]" v-for="(item, index) in menu" :key="index" @click="tabSelect" :data-id="index">{{ item.label }}({{ item.num }})</view>
				</scroll-view>
			</view>
			<view :class="[{'padding-fixed': fixed}]">
				<view class="margin-top-xs" v-if="curTab == 0">
					<Goods v-for="(item, index) in list" :key="index" :item="item"></Goods>
				</view>
				<view class="margin-top-xs" v-else>
					<Card v-for="(item, index) in list" :key="index" :item="item"></Card>
				</view>
			</view>
		</scroll-view>
		
	</view>
</template>

<script>
	import Goods from '@/components/goods.vue';
	import Card from '@/components/card.vue';
	export default {
		components: {
			Goods,
			Card
		},
		data() {
			return {
				customBar: 0,
				focus: {
					followNum: 100,
					focusNum: 3
				},
				menu: [{
					num: 30,
					label: '商品'
				}, {
					num: 110,
					label: '帖子'
				}],
				curTab: 0,
				scrollLeft:0,
				fixed: false, // 是否吸顶
				list: [], // 商品列表
			}
		},
		created() {
			new Array(10).fill(0).map((v, i) => this.list.push({
				id: i,
				title: '商品' + i,
				info: '我是商品介绍，介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍',
				address: '望京soho' + i,
				price: 300 + i
			}));
		},
		methods: {
			onGoPage(item) {
				if (item['link']) {
					uni.navigateTo({
						url: item.link
					})
				}
			},
			tabSelect(e) {
				this.curTab = e.currentTarget.dataset.id;
				console.log("this.curTab", this.curTab)
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			},
			// 滚动监听
			scroll(e) {
				if (e.detail.scrollTop > 170) {
					this.fixed = true;
					this.customBar = this.CustomBar;
				} else {
					this.fixed = false;
					this.customBar = 0;
				}
			},
			// 触底刷新
			onRefresh() {
				console.log('bottom');
			}
		}
	}
</script>
<style lang="scss" lang="scss">
	.padding-fixed {
		padding-top: 210upx;
	}
	.flex-1 {
		flex: 1;
	}
	.flex-align-center {
		align-items: center;
	}
	.flex-space-between {
		justify-content: space-between;
	}
	.my-header {
		.my-header_container {
			padding: 20upx 24upx 0;
			background-color: #fff;
			.user-photo {
				width: 130upx;
				height: 130upx;
				border-radius: 50%;
			}
			.my-header-right {
				view {
					margin: 10upx 0;
					color: #666883;
					font-size: 30upx;
				}
				.nickname {
					font-size: 42upx;
				}
			}
		}
	}
	.fixed {
		position: fixed;
		width: 100%;
		&.margin-top-sm {
			margin-top: 0;
			z-index: 999;
			box-shadow: 0 0 20upx rgba(0, 0, 0, .5);
		}
	}
</style>

